import { motion, useInView } from "framer-motion";
import { useRef, useState, useEffect } from "react";
import { Gamepad2, Maximize2, Video, Zap, Shield, Tv, LucideIcon } from "lucide-react";
import { isMobileDevice, prefersReducedMotion } from "../src/utils/deviceDetect";
import { useLanguage } from "../contexts/LanguageContext";
import { getTranslation } from "../translations";

interface Feature {
  title: string;
  description: string;
  icon: LucideIcon;
  size: string;
}

export function BentoFeatures() {
  const { language } = useLanguage();
  const t = getTranslation(language);
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true, margin: "-100px" });
  const [isMobile, setIsMobile] = useState(false);
  const [reducedMotion, setReducedMotion] = useState(false);

  useEffect(() => {
    setIsMobile(isMobileDevice());
    setReducedMotion(prefersReducedMotion());
  }, []);

  const animationDuration = isMobile || reducedMotion ? 0.3 : 0.5;

  const features = [
    {
      title: t.bentoFeatures.remoteControl,
      description: t.bentoFeatures.remoteControlDesc,
      icon: Gamepad2,
      size: "large",
    },
    {
      title: t.bentoFeatures.smartScaling,
      description: t.bentoFeatures.smartScalingDesc,
      icon: Maximize2,
      size: "medium",
    },
    {
      title: t.bentoFeatures.hotContent,
      description: t.bentoFeatures.hotContentDesc,
      icon: Video,
      size: "medium",
    },
    {
      title: t.bentoFeatures.lightningFast,
      description: t.bentoFeatures.lightningFastDesc,
      icon: Zap,
      size: "small",
    },
    {
      title: t.bentoFeatures.privacyFirst,
      description: t.bentoFeatures.privacyFirstDesc,
      icon: Shield,
      size: "small",
    },
    {
      title: t.bentoFeatures.privateBrowsing,
      description: t.bentoFeatures.privateBrowsingDesc,
      icon: Tv,
      size: "small",
    },
  ];

  return (
    <section className="py-32 relative overflow-hidden">
      <div className="container mx-auto px-6">
        {/* Section Header */}
        <motion.div
          ref={ref}
          initial={{ opacity: 0, y: 50 }}
          animate={isInView ? { opacity: 1, y: 0 } : {}}
          transition={{ duration: animationDuration }}
          className="text-center mb-16 will-change-transform"
        >
          <h2 className="text-5xl md:text-6xl font-bold mb-4">
            <span className="bg-gradient-to-r from-white to-cyan-400 bg-clip-text text-transparent">
              {t.bentoFeatures.title}
            </span>
          </h2>
          <p className="text-xl text-slate-400 max-w-2xl mx-auto">
            {t.bentoFeatures.subtitle}
          </p>
        </motion.div>

        {/* Bento Grid */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4 max-w-7xl mx-auto">
          {/* Large Feature - Spans 2x2 */}
          <motion.div
            initial={{ opacity: 0, scale: 0.9 }}
            animate={isInView ? { opacity: 1, scale: 1 } : {}}
            transition={{ duration: animationDuration, delay: isMobile || reducedMotion ? 0.05 : 0.1 }}
            className="md:col-span-2 md:row-span-2 group"
          >
            <FeatureCard feature={features[0]} large isMobile={isMobile} reducedMotion={reducedMotion} />
          </motion.div>

          {/* Medium Features - Span 2x1 */}
          <motion.div
            initial={{ opacity: 0, scale: 0.9 }}
            animate={isInView ? { opacity: 1, scale: 1 } : {}}
            transition={{ duration: animationDuration, delay: isMobile || reducedMotion ? 0.1 : 0.2 }}
            className="md:col-span-2 group"
          >
            <FeatureCard feature={features[1]} isMobile={isMobile} reducedMotion={reducedMotion} />
          </motion.div>

          {/* Small Features - Span 1x1 */}
          <motion.div
            initial={{ opacity: 0, scale: 0.9 }}
            animate={isInView ? { opacity: 1, scale: 1 } : {}}
            transition={{ duration: animationDuration, delay: isMobile || reducedMotion ? 0.15 : 0.3 }}
            className="group"
          >
            <FeatureCard feature={features[3]} isMobile={isMobile} reducedMotion={reducedMotion} />
          </motion.div>

          <motion.div
            initial={{ opacity: 0, scale: 0.9 }}
            animate={isInView ? { opacity: 1, scale: 1 } : {}}
            transition={{ duration: animationDuration, delay: isMobile || reducedMotion ? 0.2 : 0.4 }}
            className="group"
          >
            <FeatureCard feature={features[4]} isMobile={isMobile} reducedMotion={reducedMotion} />
          </motion.div>

          {/* Medium Feature */}
          <motion.div
            initial={{ opacity: 0, scale: 0.9 }}
            animate={isInView ? { opacity: 1, scale: 1 } : {}}
            transition={{ duration: animationDuration, delay: isMobile || reducedMotion ? 0.25 : 0.5 }}
            className="md:col-span-2 group"
          >
            <FeatureCard feature={features[2]} isMobile={isMobile} reducedMotion={reducedMotion} />
          </motion.div>

          {/* Small Feature */}
          <motion.div
            initial={{ opacity: 0, scale: 0.9 }}
            animate={isInView ? { opacity: 1, scale: 1 } : {}}
            transition={{ duration: animationDuration, delay: isMobile || reducedMotion ? 0.3 : 0.6 }}
            className="md:col-span-2 group"
          >
            <FeatureCard feature={features[5]} isMobile={isMobile} reducedMotion={reducedMotion} />
          </motion.div>
        </div>
      </div>
    </section>
  );
}

function FeatureCard({ feature, large = false, isMobile = false, reducedMotion = false }: {
  feature: Feature;
  large?: boolean;
  isMobile?: boolean;
  reducedMotion?: boolean;
}) {
  const Icon = feature.icon;

  return (
    <div
      className={`
        relative h-full rounded-3xl p-8 backdrop-blur-xl
        bg-gradient-to-br from-slate-900/50 to-slate-800/30
        border border-slate-700/50
        overflow-hidden
        transition-all duration-500
        group-hover:border-cyan-400/50
        group-hover:shadow-2xl group-hover:shadow-cyan-500/20
        ${large ? "min-h-[400px]" : "min-h-[200px]"}
      `}
    >
      {/* Gradient Overlay */}
      <div
        className="absolute inset-0 opacity-0 group-hover:opacity-10 bg-gradient-to-br from-cyan-400 to-blue-600 transition-opacity duration-500"
      />

      {/* Content */}
      <div className="relative z-10 h-full flex flex-col">
        {/* Icon - Simple circle with glow */}
        <div className="mb-6">
          <motion.div
            whileHover={!isMobile && !reducedMotion ? { scale: 1.1 } : {}}
            className={`
              inline-flex items-center justify-center
              rounded-full
              border-2 border-cyan-400/30
              bg-cyan-400/5
              backdrop-blur-sm
              ${large ? "w-20 h-20" : "w-16 h-16"}
              group-hover:border-cyan-400/60
              group-hover:bg-cyan-400/10
              group-hover:shadow-lg group-hover:shadow-cyan-500/50
              transition-all duration-500
            `}
          >
            <Icon className={`${large ? "w-10 h-10" : "w-8 h-8"} text-cyan-400 stroke-[1.5]`} />
          </motion.div>
        </div>

        {/* Text */}
        <div className="flex-1">
          <h3 className={`${large ? "text-3xl md:text-4xl" : "text-2xl"} font-bold mb-3 text-white`}>
            {feature.title}
          </h3>
          <p className={`${large ? "text-lg" : "text-base"} text-slate-300`}>
            {feature.description}
          </p>
        </div>

        {/* Decorative Elements */}
        <div className="absolute top-0 right-0 w-32 h-32 bg-gradient-to-br from-cyan-400/10 to-transparent rounded-full blur-3xl" />
        <div className="absolute bottom-0 left-0 w-24 h-24 bg-gradient-to-tr from-blue-600/10 to-transparent rounded-full blur-2xl" />
      </div>

      {/* Hover Glow Effect */}
      <div
        className="absolute inset-0 opacity-0 group-hover:opacity-100 bg-gradient-to-br from-cyan-400/5 to-blue-600/5 blur-3xl -z-10 transition-opacity duration-500"
      />
    </div>
  );
}

export default BentoFeatures;